"use client";

import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query";
import axiosInstance from "@/lib/axiosInstance";

export interface JobApplication {
  _id: string;
  title: string;
  company: string;
  status: "applied" | "1st_round" | "2nd_round" | "selected" | "rejected";
  adminFeedback?: string;
}

const fetchUserApplications = async (): Promise<JobApplication[]> => {
  const res = await axiosInstance.get("/applications/my");
  return res.data;
};

const applyForJob = async (jobId: string): Promise<{ message: string }> => {
  const res = await axiosInstance.post(`/applications/${jobId}/apply`);
  return res.data;
};

interface UpdateStatusPayload {
  id: string;
  status: JobApplication["status"];
  feedback?: string;
}

const updateApplicationStatus = async ({
  id,
  status,
  feedback,
}: UpdateStatusPayload): Promise<JobApplication> => {
  const res = await axiosInstance.put(`/applications/${id}/status`, {
    status,
    adminFeedback: feedback,
  });
  return res.data;
};

export const useUserApplications = () =>
  useQuery<JobApplication[]>({
    queryKey: ["userApplications"],
    queryFn: fetchUserApplications,
  });

export const useApplyJob = () => {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: applyForJob,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["userApplications"] });
    },
  });
};

export const useUpdateApplicationStatus = () => {
  const queryClient = useQueryClient();
  return useMutation({
    mutationFn: updateApplicationStatus,
    onSuccess: () => {
      queryClient.invalidateQueries({ queryKey: ["userApplications"] });
    },
  });
};
